				"html": "<h2>The steps need to follow for sending the file.</h2>\n<ol>\n<li>Make sure both the computers connected through same network via LAN or WIFI.</li>\n<li>Open terminal in Ubuntu and make sure you have python installed in your PC.</li>\n<li>If not installed then install it by typing in terminal \"sudo apt-get install python\" without quotes.</li>\n<li>Goto the directory whose file you want to share by using cd(change directory) command.</li>\n<li>Type this command \"python -m simpleHTTPserver\" without quotes.</li>\n<li>Open new terminal and type ifconfig and find your IP address.</li>\n</ol>\n<h2>Now in second computer</h2>\n<ol>\n<li>Open browser and type the ip address of first one.</li>\n<li>Don't forget to add port number at end of ip address..which by default is:8000</li>\n</ol>\n<p>A page will open showing the Directory type structure and it will show all files from source pc.<br>\nNow you can access all files.</p>",
